<template>
  <div class="header">
    <!--  -->
    <div class="logo-div">
      <div class="logo-font"></div>
    </div>
    <div class="login-out" title="" @click="logout"></div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {};
  },
  mounted() {},
  methods: {
    logout() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        this.$post($url.DATA_URL + "/logout").then((res) => {
          if (res.data.code == 200) {
            this.$router.push("/");
          } else {
            this.$message({
              type: "error",
              message: res.data.msg,
            });
          }
        });
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.header {
  width: 100%;
  height: 120px;
  // background: #2b315c;
  background: #171b22;
  border-radius: 3px;
  // padding: 0 50px 30px 50px;
  box-sizing: border-box;
  // 
  .logo-div {
    height: 120px;
    line-height: 120px;
    padding: 0 30px;
    .logo-font {
      color: #fff;
      font-size: 50px;
      font-family: "MyFont";
      height: 115px;
      width: 400px;
      background: url("../../assets/images/logobk.png") 100% 100%;
    }
    .bottom-line {
      height: 140px;
      background: #fff;
      // transform-origin: left top;
      width: 100%;
      // transform: rotateZ(35deg);
    }
    .line-left::before {
      content: "";
      position: absolute;
      height: 2px; // 
      width: 240px; // 
      background: linear-gradient(
        to right,
        transparent,
        #fff
      ); // 
      animation: fade-left 3s linear infinite; //fade-left
      box-shadow: 0px 0px 10px 2px #72dffa; // 
    }

    // rightleft
    @keyframes fade-left {
      0% {
        right: calc(100% - 40px); // 
        opacity: 0;
      }
      10% {
        opacity: 1;
      }
      90% {
        opacity: 1;
      }
      100% {
        right: 0px;
        opacity: 0;
      }
    }
  }
  .login-out {
    width: 30px;
    height: 30px;
    background: url("../../assets/icons/logout.png") no-repeat center;
    background-size: 100% 100%;
    position: absolute;
    right: 25px;
    top: 25px;
    cursor: pointer;
    z-index: 999;
  }
}
</style>
